.pagination{
	display: flex;
	align-items: center;
	&__link{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 35px;
		height: 35px;
		font-weight: 500;
		font-size: 14px;
		line-height: 130%;
		color: #3F4E65;
		cursor: pointer;
		margin: 0 -5px 0 0;
		&_active{
			background: linear-gradient(90deg, rgba(255, 198, 80, 0.3) 0%, rgba(254, 202, 110, 0.3) 97.25%);
			border-radius: 50%;
			cursor: normal;
		}
	}
}